import { request, requestDownload } from 'utils';

const prefix = '/api/students';

// export const getCodeStudent = id =>
//   request({
//     prefix,
//     url: `/Student/GetByCode/${id}`,
//     method: 'GET',
//   });
export const getCodeStudent = ({ code, semesterId, yearEduId }) =>
  request({
    prefix,
    url: `/Student/GetBEStudentByCode`,
    params: {
      code,
      semesterId,
      yearEduId,
    },
    method: 'GET',
  });
export const getBonusType = () =>
  request({
    prefix: '/api/categories',
    url: `/BonusType`,
    method: 'GET',
  });
export const getBonusTypeChild = id =>
  request({
    prefix: '/api/categories',
    url: `/BonusType/GetBonusTypeChild`,
    params: {
      id,
    },
    method: 'GET',
  });

export const addItemClassReward = dataAddReward =>
  request({
    prefix,
    url: `/BonusNote/CreateBNForClass`,
    data: {
      ...dataAddReward,
    },
    method: 'POST',
  });

export const getListClassReward = params =>
  request({
    prefix,
    url: `/BonusNote/GetBNClasses`,
    params: {
      ...params,
    },
    method: 'GET',
  });

export const getListClass = params =>
  request({
    prefix,
    url: `/BonusNote/BrowseBonusEstimationClass`,
    params: {
      ...params,
    },
    method: 'GET',
  });

export const exportDataAddReward = params =>
  requestDownload({
    defaultFileName: 'ExportBonusNote.pdf',
    prefix,
    url: `/BonusNote/ExportBonusNote`,
    data: {
      ...params,
    },
    method: 'POST',
  });

export const getAddRewardById = id =>
  request({
    prefix,
    url: `/BonusNote/BNForClass/${id}`,
    method: 'GET',
  });



