import React, { Component, Fragment } from 'react';
import { Form, Col } from 'antd';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import SelectAjax from './SelectAjax';

export default class YearSemesterInput extends Component {
  state = {};

  componentWillReceiveProps = () => {
    // let yearEduId = nextProps.form.getFieldValue('yearEduId');
    // let semesterId = nextProps.form.getFieldValue('semesterId');
    // if (yearEduId && typeof yearEduId !== 'number') {
    //   yearEduId = parseInt(yearEduId);
    //   nextProps.form.setFieldsValue({ yearEduId });
    // }
    // if (semesterId && typeof semesterId !== 'number') {
    //   semesterId = parseInt(semesterId);
    //   nextProps.form.setFieldsValue({ semesterId });
    // }
  };
  render() {
    const { disabled, optionFieldDecorator = {}, span = 6 } = this.props;
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const yearEduId = getFieldValue('yearEduId');
    return (
      <Fragment>
        <Col xs={24} sm={12} md={span}>
          <Form.Item label="Năm học">
            {getFieldDecorator('yearEduId', optionFieldDecorator.yearEduId)(
              <SelectAjax
                disabled={!!disabled}
                service={getYearEducations}
                setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                typeValue="int"
              />
            )}
          </Form.Item>
        </Col>
        <Col xs={24} sm={12} md={span}>
          <Form.Item label="Học kì">
            {getFieldDecorator('semesterId', optionFieldDecorator.semesterId)(
              <SelectAjax
                disabled={!!disabled}
                setFieldsValue={setFieldsValue}
                service={getSemesters}
                params={{ yearEduId }}
                allowGetData={!!yearEduId}
                typeValue="int"
              />
            )}
          </Form.Item>
        </Col>
      </Fragment>
    );
  }
}



