import { request } from 'utils';

const prefix = '/api/categories';

export const getSemesters = ({ label, page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: '/Semester',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getSemesterById = id =>
  request({
    prefix,
    url: `/Semester/${id}`,
    method: 'GET',
  });

export const deleteSemester = id =>
  request({
    prefix,
    url: `/Semester/${id}`,
    method: 'DELETE',
  });

export const updateSemester = ({ id, label, isMain }) =>
  request({
    prefix,
    url: `/Semester/${id}`,
    data: { id, label, isMain },
    method: 'PUT',
  });

export const createSemester = ({ label, isMain = false }) =>
  request({
    prefix,
    url: `/Semester`,
    data: { label, isMain },
    method: 'POST',
  });



