import React, { Component } from 'react';
import { Button } from 'antd';
import './style.scss';

export default class BtnAddNew extends Component {
  states = '';

  render() {
    return (
      <Button
        {...this.props}
        className={`btnAction btnAddNew ${this.props.className}`}
        icon="plus"
        size="large"
      >
        {this.props.title}
      </Button>
    );
  }
}

BtnAddNew.defaultProps = {
  className: '',
  title: 'Thêm mới',
};



