import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { isEmpty } from 'lodash';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import { Row, Card, Button, Table, Col, Form, Input, Select, Spin, message } from 'antd';
import BtnAddNew from 'components/common/button/BtnAddNew';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import DatePickerString from 'components/common/input/DatePickerString';
import {
  getBonusType,
  getBonusTypeChild,
  addItemClassReward,
  getAddRewardById,
  getListClass,
} from 'services/ClassReward';
import { getQueryParams } from 'utils/urlUtil';

const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormAddReward extends Component {
  state = {
    teacherId: false,
    initialValue: {},
    dataAddReward: {},
    isPending: false,
    bonusType: [],
    bonusTypeChild: {},
    bonusTypeChildNew: [],
    listOptionClass: [],
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    getBonusType().then(res => {
      this.setState({
        bonusType: res.data.items,
      });
    });
    if (queryParamId) {
      this.setState({ teacherId: queryParamId });
      this.getAddRewardById(queryParamId);
    }
  };

  getListClassId = () => {
    this.props.form.validateFields(['yearEduId', 'semesterId'], (err, values) => {
      if (err) {
        return;
      }
      getListClass(values).then(res => {
        this.setState({
          listOptionClass: res.data.items,
        });
      });
    });
  };

  getAddRewardById = async id => {
    const { form } = this.props;
    const dataMap = await getAddRewardById(id);
    const initialValue = handleInitialValue(dataMap.data);
    const { semesterId, yearEduId } = dataMap.data;
    const params = {
      semesterId,
      yearEduId,
      isEdit: true,
    };
    await getListClass(params).then(res => {
      this.setState({
        listOptionClass: res.data.items,
      });
    });
    await dataMap.data.classes.forEach(classs => {
      this.chooseBonusType(classs.parentBonusTypeId);
    });
    form.setFieldsValue(initialValue);
    const { listOptionClass } = this.state;
    const dataClassReward = {};
    dataMap.data.classes.forEach(classItem => {
      const randomCode = this.makeid(8);
      const valueClass = listOptionClass.filter(item => item.classId === classItem.classId);
      const newClassValue = {
        randomCode,
        noOfStudent: valueClass[0].noOfStudent,
        noOfGreat: valueClass[0].noOfGreat,
        noOfGood: valueClass[0].noOfGood,
        noOfExcellent: valueClass[0].noOfExcellent,
      };
      dataClassReward[randomCode] = {
        ...newClassValue,
        ...classItem,
      };
    });
    this.setState({ initialValue, dataAddReward: dataClassReward });
  };

  handleSubmit = () => {
    const { dataAddReward, teacherId } = this.state;
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (isEmpty(dataAddReward)) {
          message.warning('Phải có sinh viên được chọn!');
          return;
        }
        if (!Object.values(dataAddReward)[0].classId) {
          message.warning('Phải chọn lớp !');
          return;
        }
        if (teacherId) {
          const data = [];
          Object.values(dataAddReward).forEach(item => {
            const { classId, bonusTypeId } = item;
            const dataAddnew = {
              classId,
              bonusTypeId,
            };

            data.push(dataAddnew);
          });

          const addRewardNew = {
            ...values,
            id: teacherId,
            classes: data,
          };
          addItemClassReward(addRewardNew).then(() => {
            this.props.history.goBack();
          });
        } else {
          const data = [];
          Object.values(dataAddReward).forEach(item => {
            const { classId, bonusTypeId } = item;
            const dataAddnew = {
              classId,
              bonusTypeId,
            };

            data.push(dataAddnew);
          });

          const addRewardNew = {
            ...values,
            classes: data,
          };
          addItemClassReward(addRewardNew).then(() => {
            this.props.history.goBack();
          });
        }
      }
    });
  };

  addDataAddReward = () => {
    const { dataAddReward } = this.state;
    const randomCode = this.makeid(8);

    dataAddReward[randomCode] = {
      randomCode,
    };
    this.getListClassId();
    this.setState({
      dataAddReward,
    });
  };

  changeOptionClass = (record, value) => {
    const { dataAddReward, listOptionClass } = this.state;
    const classOption = listOptionClass.filter(item => item.classId === value);
    const { randomCode } = record;
    dataAddReward[randomCode] = {
      randomCode,
      ...classOption[0],
    };
    this.setState({
      dataAddReward,
    });
  };

  deleteData = record => {
    const { dataAddReward } = this.state;
    delete dataAddReward[record.randomCode];
    this.setState({
      dataAddReward,
    });
  };
  changeBonusTypeChild = (value, record) => {
    const { dataAddReward, bonusTypeChildNew } = this.state;
    const { randomCode, parentBonusTypeId } = record;
    dataAddReward[randomCode].bonusTypeId = value;
    this.setState({
      dataAddReward,
      bonusTypeChild: {
        ...this.state.bonusTypeChild,
        [parentBonusTypeId]: bonusTypeChildNew,
      },
    });
  };

  chooseBonusType = (value, record = '') => {
    const { dataAddReward } = this.state;
    const { randomCode } = record;
    this.setState({
      isPending: true,
    });
    getBonusTypeChild(value).then(res => {
      this.setState({
        bonusTypeChild: {
          ...this.state.bonusTypeChild,
          [value]: res.data.items,
        },
        bonusTypeChildNew: res.data.items,
      });
      this.setState({
        isPending: false,
      });
      if (record) {
        // eslint-disable-next-line array-callback-return
        dataAddReward[randomCode].parentBonusTypeId = value;
        dataAddReward[randomCode].bonusTypeId = '';
        this.setState({
          dataAddReward,
        });
      }
    });
  };

  makeid = length => {
    let result = '';
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    for (let i = 0; i < length; i += +1) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  };

  render() {
    const {
      initialValue,
      dataAddReward,
      isPending,
      bonusType,
      teacherId,
      bonusTypeChild,
      bonusTypeChildNew,
      listOptionClass,
    } = this.state;
    const { getFieldDecorator } = this.props.form;
    const columns = [
      {
        title: 'Lớp',
        dataIndex: 'classId',
        key: 'classId',
        // eslint-disable-next-line no-unused-vars
        render: (text, record, index) => (
          <Select
            style={{ width: '100%' }}
            defaultValue={record.classId}
            onChange={(value, option) => this.changeOptionClass(record, value, option)}
          >
            {listOptionClass.map(item => (
              <Option key={item.classId} value={item.classId}>
                {item.className}
              </Option>
            ))}
          </Select>
        ),
      },
      {
        title: 'Sỉ số',
        dataIndex: 'noOfStudent',
        key: 'noOfStudent',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'HS khá',
        dataIndex: 'noOfGreat',
        key: 'noOfGreat',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'HS giỏi',
        dataIndex: 'noOfGood',
        key: 'noOfGood',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'HS xuất sắc',
        dataIndex: 'noOfExcellent',
        key: 'noOfExcellent',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Danh hiệu',
        dataIndex: 'gradeTitle',
        key: 'gradeTitle',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Cấp khen thưởng',
        render: (text, record) => (
          <Select
            style={{ width: '100%' }}
            defaultValue={record.parentBonusTypeId}
            onChange={e => this.chooseBonusType(e, record)}
          >
            {bonusType.map(item => (
              <Option key={item.id} value={item.id}>
                {item.label}
              </Option>
            ))}
          </Select>
        ),
      },
      {
        title: 'Loại khen thưởng',
        render: (text, record) => (
          <Select
            style={{ width: '100%' }}
            value={record.bonusTypeId}
            onChange={e => this.changeBonusTypeChild(e, record)}
          >
            {record.bonusTypeId && record.approvalId
              ? (bonusTypeChild[record.parentBonusTypeId] || []).map(item => (
                  // eslint-disable-next-line react/jsx-indent
                  <Option key={item.id} value={item.id}>
                    {item.label}
                  </Option>
                ))
              : bonusTypeChildNew.map(item => (
                  // eslint-disable-next-line react/jsx-indent
                  <Option key={item.id} value={item.id}>
                    {item.label}
                  </Option>
                ))}
          </Select>
        ),
      },
      {
        title: 'Thao tác',
        dataIndex: 'rewardType',
        render: (text, record) => (
          <Button
            className="btn-delete"
            icon="delete"
            size="small"
            onClick={() => this.deleteData(record)}
          />
        ),
      },
    ];
    const optionFieldDecorator = {
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Card title={teacherId ? 'Cập nhật khen thưởng' : 'Thêm khen thưởng'}>
        <Row gutter={24}>
          <Form className="form-inline-label" layout="vertical">
            <Col>
              <YearSemesterInput
                form={this.props.form}
                optionFieldDecorator={optionFieldDecorator}
              />
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Số QĐ" hasFeedback>
                {getFieldDecorator('approvalNumber', {
                  initialValue: initialValue.approvalNumber,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Ngày QD" hasFeedback>
                {getFieldDecorator('approvalDate', {
                  initialValue: initialValue.approvalDate,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<DatePickerString placeholder="Chọn ngày" />)}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={8} style={{ marginLeft: 10 }}>
              <Form.Item label="Hình thức" hasFeedback>
                {getFieldDecorator('description', {
                  initialValue: initialValue.description,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
          </Form>
          <Col xs={24} sm={12} md={6}>
            <BtnSave onClick={() => this.handleSubmit()} htmlType="submit" />
          </Col>
        </Row>
        <Row>
          <BtnAddNew onClick={this.addDataAddReward} />
        </Row>
        <Row>
          <Col span={24}>
            <Spin spinning={isPending}>
              <Table
                onRow={this.rowTable}
                columns={columns}
                dataSource={Object.values(dataAddReward)}
                rowKey="randomCode"
              />
            </Spin>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddReward {...props} />
  </Page>
));



