import { request } from 'utils';

const prefix = '/api/categories';

export const getYearEducations = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/YearEducation',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getYearEducationById = id =>
  request({
    prefix,
    url: `/YearEducation/${id}`,
    method: 'GET',
  });

export const deleteYearEducation = id =>
  request({
    prefix,
    url: `/YearEducation/${id}`,
    method: 'DELETE',
  });

export const updateYearEducation = ({ id, fromYear, toYear }) =>
  request({
    prefix,
    url: `/YearEducation/${id}`,
    data: { id, fromYear, toYear },
    method: 'PUT',
  });

export const createYearEducation = ({ fromYear, toYear }) =>
  request({
    prefix,
    url: `/YearEducation`,
    data: { fromYear, toYear },
    method: 'POST',
  });



